<template>
  <section class="content" id="content">
    <article class="markdown-section" id="main">
      <div v-html="content"></div>
      <div id="gitalk-container"></div>
    </article>
  </section>
</template>

<script>
import 'gitalk/dist/gitalk.css'
import Gitalk from 'gitalk'
export default {
  name: 'HelloWorld',
  props: {
    title: String,
  },
  data(){
    return {
      input: '',
      editor: '',
    }
  },
  watch : {
    '$route'()
    {
      this.getPost ()
    }
  },
  methods: {
    getPost: function () {
        this.apiSetting.getPost.url = this.$route.path + '.md';
        this.request(this.apiSetting.getPost).then(
            res => {
              var url = this.config.github.blob + this.$route.path + '.md';
              var editHtml = '\n\n[:memo: Edit on Github](' + url + ')\n';
              this.input = res.data + editHtml
            },
            error => {
              this.input = error.status
              console.log(error)
            })
    },
    gitalk: function () {
      var config = this.config;
      var github = config.github;
      const gitalk = new Gitalk({
        clientID: github.clientID,
        clientSecret: github.clientSecret,
        repo: github.repo,
        owner: github.owner,
        admin: [github.owner],
        id: this.$route.path + '.md',      // Ensure uniqueness and length less than 50
        labels: [github.owner],
        distractionFreeMode: false  // Facebook-like distraction free mode
      })
      gitalk.render('gitalk-container')
    },
  },
  mounted: function ()
  {
    this.getPost ()
    this.gitalk ()
    // this.editorLink ()
  },
  computed: {
    content: function () {
      return this.fun.emoji(this.marked(this.input, { sanitize: true }))
    }
  }

}
</script>

